@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.title-page{
    position: relative;
}

.title-page::after{
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background: #26efa6;
    animation: lineTitle 350ms linear forwards;
    -webkit-animation: lineTitle 350ms linear forwards;
    -moz-animation: lineTitle 350ms linear forwards;
    -o-animation: lineTitle 350ms linear forwards;
    -ms-animation: lineTitle 350ms linear forwards;
    bottom: 0;
    left: 0;
    background: -webkit-gradient(left top,right top,color-stop(0,#26efa6),color-stop(100%,#251f14));
    background: linear-gradient(to right,#26efa6 0,#251f14 100%);
}
#menu-container .nav-bar{
    width: 100%;
    background-color: var(--bg-navbar);
    height: 100%;
    position: relative;
    transition: all 350ms;
    -webkit-transition: all 350ms;
    -moz-transition: all 350ms;
}

#menu-container  .list-menu a {
    list-style: none;
    color: #fff;
    display: block;
    text-align: center;
    text-decoration: none;
}

#menu-container .list-menu a:hover{
    color: var(--color-primary);
    text-decoration: none;
}

#menu-container .list-menu a .icon{
    display: block;
    font-size: 1.5em;
}

#menu-container .list-menu a.active{
    color: var(--color-primary);
}

#menu-container .icon-menu{
    position: fixed;
    right: 15px;
    top: 10px;
    border: 1px solid #fff;
    z-index: 2;
    background-color: var(--bg-body);
    line-height: 1;
}

@media (max-width: 768px){
    #menu-container .nav-bar.hide-mobile{
        left: -100%;
    }
    #menu-container .nav-bar.show{
        left: 0!important;
    }
}
.card-resume .item-date{
    color: var(--color-primary);
}

.card-resume {
    background-color: var(--bg-body);
    border: 1px solid var(--color-primary);
    position: relative;
    display: block;
    padding: .75rem 1.25rem;
    z-index: 1;
    list-style: none;
    border-right: 0;
    border-image: linear-gradient(to right, var(--color-primary) 0,var(--bg-body) 100%);
    border-image-slice: 1;
    position: relative;
}
.card-resume::after{
    content: "";
    background-color: var(--color-primary);
    height: 100%;
    width: 0;
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    opacity: .2;
    transition: all 350ms;
    -webkit-transition: all 350ms;
    -moz-transition: all 350ms;
}
.card-resume:hover::after{
    width: 100%;
}

.card-resume.active::before{
    content: "";
    position: absolute;
    top: 23px;
    left: -6px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: var(--color-primary);
}
#contact-page .icon{
    width: 80px;
    height: 80px;
    background-color: var(--color-primary);
    color: #000;
    font-size: 35px;
    transition: all 350ms;
    -webkit-transition: all 350ms;
    -moz-transition: all 350ms;
}

#contact-page a:hover .icon{
    opacity: 0.5;
}

#contact-page a {
    text-decoration: none;
    color: #fff;
}
#card-works {
    border: 1px solid var(--color-primary);
    border-right: 0;
    border-image: linear-gradient(to right,var(--color-primary) 0,var(--bg-body) 100%);
    border-image-slice: 1;
}

#card-works a{
    color: #fff;
    text-decoration: none;
    transition: all 350ms;
    -webkit-transition: all 350ms;
    -moz-transition: all 350ms;
    position: relative;
}

#card-works .subtitulo {
    color: var(--color-primary);
}
#list-filters label::before, label::after{
    position: absolute;
    content: "";
    height: 1px;
    background-color: var(--color-primary);
    width: 0;
    transition: all 350ms;
    -webkit-transition: all 350ms;
    -moz-transition: all 350ms;
}
#list-filters label::before{
    top: 0;
    left: 0;
}
#list-filters label::after{
    bottom: 0;
    right: 0;
}
#list-filters label.active::before, label.active::after{
    width: 100%;
}

#list-filters label{
    cursor: pointer;
    position: relative;
}
:root {
    --bg-navbar: #13100a;
    --bg-body: #251f14;
    --color-primary: #26efa6;
}

* {
    font-family: 'Roboto', sans-serif;
    box-sizing: border-box;
}

/* width */
::-webkit-scrollbar {
    width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #26efa6;
    background: var(--color-primary);
}

body,
html,
#root {
    min-height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    height: 100%;
    background-color: #251f14;
    background-color: var(--bg-body);
}

#root {
    overflow: auto;
}

/* GLOBAL VARS */
.fadeInUp {
    -webkit-animation-name: fadeInUp;
    animation-name: fadeInUp;
    -webkit-animation-duration: .5s;
    animation-duration: .5s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

@-webkit-keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translate3d(0, 100%, 0);
    }

    100% {
        opacity: 1;
        transform: none;
    }
}

@keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translate3d(0, 100%, 0);
    }

    100% {
        opacity: 1;
        transform: none;
    }
}

.fadeIn {
    -webkit-animation-name: fadeIn;
    animation-name: fadeIn;
    -webkit-animation-duration: .5s;
    animation-duration: .5s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.mh-100 {
    max-height: 100%;
}

/* NAVBAR */
.content-navbar {
    max-width: 80px;
    width: 100%;
    display: block;
    height: 100%;
    position: fixed;
    left: 0;
    z-index: 10;
}
